import {appConfig} from "@/utils/appConfig";

const state = {
	win: true,
	lost: false,
	gameLevelConfig: appConfig.getLevelConfig("beginner")
};

const actions = {
	startNewGame({dispatch, commit}: any): void {
		//ctx.dispatch("updateGameLevelConfig");

		//ctx.dispatch("initBoardState");
		//ctx.dispatch("setBoardBombCount", ctx.getters.getBombsCount);

		dispatch("timer/newTimer", null, {root: true});
		commit("startNewGame");
		dispatch("bombCount/setBombLevelCount", null, {root: true});
	},

	updateGameLevelConfig({commit, dispatch}: any, gameLevel: string) {
		commit("updateGameLevelConfig", gameLevel);
		dispatch("startNewGame");
	}
};

const mutations = {
	startNewGame(state: {win: boolean; lost: boolean}): void {
		state.win = false;
		state.lost = false;
	},
	updateGameLevelConfig(state: any, gameLevel: string) {
		state.gameLevelConfig = appConfig.getLevelConfig(gameLevel);
	},
	setWin(state: any) {
		state.win = true;
	},
	setLost(state: any) {
		state.lost = true;
	}
};

const getters = {
	getBombsCount(state: any) {
		return state.gameLevelConfig.bombCount;
	},
	getWidthCellCount(state: any) {
		return state.gameLevelConfig.widthCellCount;
	},
	getHeightCellCount(state: any): string {
		return state.gameLevelConfig.heightCellCount;
	},
	isWin(state: any) {
		return state.win;
	},
	isLost(state: any) {
		return state.lost;
	}
};

export default {
	namespaced: true,
	state,
	actions,
	mutations,
	getters
};
