const state = {
	bombLevelCount: undefined,
	bombRemainCount: undefined
};

const actions = {
	setBombLevelCount({rootGetters, commit}: any, num: any) {
		let bombCount: number = rootGetters["game/getBombsCount"];

		commit("setBombLevelCount", bombCount);
	}
};

const mutations = {
	setBombLevelCount(state: any, num: number) {
		state.bombLevelCount = num;
		state.bombRemainCount = num;
	}
};

const getters = {
	bombCount(state: any) {
		return state.bombRemainCount;
	}
};

export default {namespaced: true, state, actions, mutations, getters};
